.user-circle-image {
  width: 65px;
  height: 65px;
  border-radius: 100px;
  overflow: hidden;
}

.playlist-image {
  width: 65px;
  height: 65px;
  border-radius: 10px;
  border: dashed 2px white;
  overflow: hidden;
}

.search-box {
  border-radius: 50px;
}

.background-image {
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.profile-image {
  width: 160px;
  height: 160px;
}

.profile-position {
  top: -80px;
}

body {
  /* background-color: #bfd1e5 !important; */
  background: repeating-linear-gradient(
    70deg,
    #647aa3,
    #647aa3 30px,
    #7e91b4 30px,
    #7e91b4 60px
  );
}

.webdv-playlist-style {
  background-color: #ecf2f3;
  border-bottom: solid 6px #d9e5e7;
  border-right: solid 3px #d9e5e7;
  border-radius: 13px;
}

.webdv-small-playlist-style {
  background-color: #c4bcc8;
  /* border: solid 2px #94849b; */
  border-bottom: solid 4px #94849b;
  /* border-right: solid 2px #94849b; */
  border-radius: 13px;
}

.webdv-postgen-style {
  background-color: #f7f7f7;
  border-bottom: solid 6px #ebebeb;
  border-right: solid 3px #ebebeb;
  border-radius: 13px;
}

.webdv-title-style {
  background-color: #fdfcfc;
  border-bottom: solid 4px #ebebeb;
  border-right: solid 2px #ebebeb;
  border-radius: 13px;
}
